<style lang="less" scoped>
  .li-item {
    width: 100%;
    margin-bottom: 10px;
    i {
      display: inline-block;
      margin-right: 8px;
      width: 8px;
      height: 8px;
      border-radius: 8px;
      background-color: #333;
    }
    span {
      display: inline-block;
      margin-right: 10px;
      color: #333;
    }
  }
  .dialog-tabel-item {
    p {
      font-size: 12px;
      color: #666666;
      line-height: normal;
      margin-bottom: 5px;
    }
    @{global} {
      .el-form-item{
        margin-right: 6px;
        &:last-child{
          margin-right: 0;
        }
      }
      .el-input__inner {
        padding: 0 6px;
        width: 200px;
        font-size: 12px;
      }
      .el-form-item__content{
        line-height: 30px;
      }
      .disblock{
        display: block;
        .el-form-item__content {
          display: block;
        }
        .el-textarea__inner {
          padding-left: 6px;
          font-size: 12px;
        }
      }
    }
  }
</style>
<template>
  <div class="page-box">
    <top-header title="外部用户登录方式配置">
      <div class="el-icon-plus" @click="onAdd">新增</div>
    </top-header>
    <div class="page-container">
      <div class="table-filters">
        <div class="filter-item">
          <p class="item">外部用户名称</p>
          <el-input v-model="filterCondition.content"
                    clearable
                    size="small"
                    placeholder="请输入"></el-input>
        </div>
        <div class="filter-item">
          <p class="item">所属部门</p>
          <el-select v-model="filterCondition.associated_system_id" size="small" placeholder="请选择">
            <el-option v-for="(item, key) in [{name: '不限',id: ''}, ...selectSys]" :key="key" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </div>
        <div class='search-btn'>
          <el-button type="primary" size="small" icon="el-icon-search" @click="handleSizeChange()">查询</el-button>
        </div>
      </div>
      <el-table
        :data="approvalData"
        size="mini"
        style="width: 100%">
        <el-table-column
          label="序号"
          width="60">
          <template slot-scope="scope">
            {{(page.current - 1) * page.size + scope.$index + 1}}
          </template>
        </el-table-column>
        <el-table-column
          prop="associated_system_name"
          label="外部用户名称">
        </el-table-column>
        <el-table-column
          prop="associated_system_name"
          label="用户数量">
        </el-table-column>
        <el-table-column
          prop="associated_system_name"
          label="所属部门">
        </el-table-column>
        <el-table-column
          prop="associated_system_name"
          label="登录系统">
        </el-table-column>
        <el-table-column
          prop="associated_system_name"
          label="登录方式">
        </el-table-column>
        <el-table-column
          prop="remark"
          label="备注">
        </el-table-column>
        <el-table-column
          fixed="right"
          label="操作"
          width="90">
          <template slot-scope="scope">
            <el-button size="mini" type="text" @click="onEdit(scope.row)">修改</el-button>
            <el-button size="mini" type="text">
              <kye-confirm
                label="删除"
                title="删除审批流程配置"
                :content="`确定要删除${scope.row.associated_system_name}系统审批流程配置？`"
                :callback="onDelete"
                :scope-data="scope.row">
              </kye-confirm>
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        background
        v-show="cptPagination"
        @size-change="handleSizeChange"
        @current-change="handleCurrentPageChange"
        :page-sizes="page.sizes"
        layout="sizes,prev,pager,next"
        :current-page.sync="page.current"
        :total="page.total"
        prev-text="上一页"
        next-text='下一页'>
      </el-pagination>
    </div>
    <kye-dialog
      width="450px"
      @is-show-dialog="isShowDialog"
      @reset="onReset('ruleForm')"
      :hasDialog="kyeDialog.hasDialog"
      :title="kyeDialog.title">
      <div slot="body">
        <el-form
          :inline="true"
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm">
          <div class="dialog-tabel-item">
            <el-form-item prop="associated_system_id">
              <p>所属系统</p>
              <el-input v-model="ruleForm.remark"
                        clearable
                        size="small"
                        placeholder="请输入备注">
              </el-input>
            </el-form-item>
            <el-form-item prop="remark">
              <p>所属部门</p>
              <el-input v-model="ruleForm.remark"
                        clearable
                        size="small"
                        placeholder="请输入备注">
              </el-input>
            </el-form-item>
          </div>
          <div class="dialog-tabel-item">
            <el-form-item prop="remark">
              <p>登录系统</p>
              <el-input v-model="ruleForm.remark"
                        clearable
                        size="small"
                        placeholder="请输入备注">
              </el-input>
            </el-form-item>
            <el-form-item prop="checked">
              <p>默认登录方式</p>
              <el-select
                v-model="ruleForm.checked"
                size="small"
                placeholder="请选择">
                <el-option v-for="(item, index) in {2: {name: '手机验证码登录'}}"
                           :value="index + 1"
                           :label="item.name"
                           :key="index">
                </el-option>
              </el-select>
            </el-form-item>
          </div>
          <div class="dialog-tabel-item">
            <p>备注</p>
            <el-form-item size="small" name="remark" class="disblock">
              <el-input style="display: block;" type="textarea" :autosize="{ minRows: 1, maxRows: 3 }" placeholder="请输入系统备注" v-model="ruleForm.remark"></el-input>
            </el-form-item>
          </div>
        </el-form>
      </div>
      <div slot="footer">
        <el-button size="small" @click="kyeDialog.hasDialog = false">取消</el-button>
        <el-button type="primary" size="small" @click="submitForm('ruleForm')">保存</el-button>
      </div>
    </kye-dialog>
  </div>
</template>

<script>
import KyeDialog from '@/components/dialog'

export default {
  components: {
    KyeDialog
  },
  data () {
    return {
      kyeDialog: {
        hasDialog: false,
        title: '新增'
      },
      // 查询条件
      filterCondition: {
        associated_system_id: '',
        page: 1,
        per_page: 10
      },
      // dialog type  ADD/EDIT
      handleType: '',
      approvalData: [],
      page: {
        total: 0,
        size: 10,
        current: 1,
        sizes: [10, 20, 50, 100]
      },
      selectSys: [],
      defaultRuleForm: '',
      onEditEmpty: '',
      ruleForm: {
        id: 0,
        associated_system_id: '',
        remark: '',
        checked: ''
      },
      rules: {
        associated_system_id: [{ required: true, message: '请选择系统名称', trigger: 'blur' }],
        approval_flow: [
          {
            approval_type: [{ required: true, message: '请选择一级审批', trigger: 'blur' }],
            approval_user_name: [{ required: true, message: '请输入审批人', trigger: 'change' }],
            approval_role_name: [{ required: true, message: '请输入审批人', trigger: 'change' }]
          },
          {
            approval_type: [{ required: true, message: '请选择二级审批', trigger: 'blur' }],
            approval_user_name: [{ required: true, message: '请输入审批人', trigger: 'change' }],
            approval_role_name: [{ required: true, message: '请输入审批人', trigger: 'change' }]
          },
          {
            approval_type: [{ required: true, message: '请选择三级审批', trigger: 'blur' }],
            approval_user_name: [{ required: true, message: '请输入审批人', trigger: 'change' }],
            approval_role_name: [{ required: true, message: '请输入审批人', trigger: 'change' }]
          },
          {
            approval_type: [{ required: true, message: '请选择四级审批', trigger: 'blur' }],
            approval_user_name: [{ required: true, message: '请输入审批人', trigger: 'change' }],
            approval_role_name: [{ required: true, message: '请输入审批人', trigger: 'change' }]
          },
          {
            approval_type: [{ required: true, message: '请选择五级审批', trigger: 'blur' }],
            approval_user_name: [{ required: true, message: '请输入审批人', trigger: 'change' }],
            approval_role_name: [{ required: true, message: '请输入审批人', trigger: 'change' }]
          }
        ]
      },
      restaurants: {
        users: [],
        roles: []
      },
      state4: '',
      timeout: null
    }
  },
  created () {
    // 保存默认数据
    this.defaultRuleForm = JSON.parse(JSON.stringify(this.ruleForm))
    this.getSearchData()
    this.$API.getConfigurationApprovalSys().then(res => {
      if (res.code === 200) {
        this.selectSys = res.result
        // this.selectSys = [{'id': 1, 'name': 'ERP'}, {'id': 2, 'name': 'OA'}]
      }
    })
  },
  computed: {
    // 如果总条数大于默认显示数
    cptPagination: function () {
      return this.page.total >= 10
    }
  },
  methods: {
    onAdd () {
      this.handleType = 'ADD'
      this.ruleForm = JSON.parse(JSON.stringify(this.defaultRuleForm))
      this.onReset('ruleForm')
      this.kyeDialog.title = '新增审批流程'
      this.isShowDialog(true)
    },
    isShowDialog (bool) {
      this.kyeDialog.hasDialog = bool
    },
    getSearchData () {
      this.$API.getConfigurationApprovalList(this.filterCondition).then(res => {
        if (res.code === 200) {
          this.approvalData = res.result.data
          this.page.total = res.result.pagination.total_count
        }
      })
    },
    // 查询按钮 和 列表条数
    handleSizeChange (val) {
      // 如果val为空表示通过查询按钮触发，使用当前选中的条数
      if (val) {
        this.filterCondition.per_page = val
      }
      this.page.current = this.filterCondition.page = 1
      this.getSearchData()
    },
    // 当前页
    handleCurrentPageChange (val) {
      this.filterCondition.page = val
      this.getSearchData()
    },
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          switch (this.handleType) {
            case 'ADD':
              this.addConfig(this.ruleForm)
              break
            case 'EDIT':
              this.editConfig(this.ruleForm)
              break
            default:
              console.log(this.handleType, '=====')
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    onAddStep (index) {
      if (this.ruleForm.approval_flow.length >= 5) return
      if (this.ruleForm.approval_flow.length > (index + 1)) return
      let tempIndex = index + 2
      this.ruleForm.approval_flow.push({
        level: tempIndex,
        approval_type: undefined,
        approval_user_name: '',
        approval_user_id: undefined,
        approval_role_id: undefined,
        approval_role_name: ''
      })
    },
    onDelStep (index) {
      this.ruleForm.approval_flow.splice(index, 1)
    },
    onEdit (row) {
      this.handleType = 'EDIT'
      this.$API.getConfigurationApprovalId({id: row.id}).then(res => {
        if (res.code === 200) {
          this.ruleForm = res.result
          this.kyeDialog.title = '修改审批流程'
          this.isShowDialog(true)
        }
      })
    },
    onReset (formName) {
      if (!this.$refs[formName]) return false
      this.$refs[formName].resetFields()
    },
    filterKey (arr, key) {
      return arr.map(a => this.$utils.checkStep(a[[key]]))
    },
    querySearchAsyncUser (queryString, cb) {
      this.$API.getUserList().then(res => {
        let restaurants = res.result.data
        let results = queryString ? restaurants.filter(this.createStateFilter(queryString)) : restaurants
        cb(results)
      })
    },
    querySearchAsyncRole (queryString, cb) {
      let RoleType = Object.keys(this.$store.state.roleTypes)[5]
      this.$API.getRoleList({type: RoleType}).then(res => {
        let restaurants = res.result.data
        let results = queryString ? restaurants.filter(this.createStateFilter(queryString)) : restaurants
        cb(results)
      })
    },
    createStateFilter (queryString) {
      return (state) => {
        return (state.name.toLowerCase().indexOf(queryString.toLowerCase()) === 0)
      }
    },
    handleSelect (item, index, type) {
      // 3:指定角色 4:指定人员
      if (type === 3) {
        this.ruleForm.approval_flow[index].approval_role_id = item.id
      } else {
        this.ruleForm.approval_flow[index].approval_user_id = item.id
      }
    },
    handleChange (id, index) {
      // this.ruleForm.approval_flow[index].approval_user_name = ''
      // this.ruleForm.approval_flow[index].approval_role_name = ''
    },
    onDelete (id) {
      return this.$API.delConfigurationApprovalId(id).then(res => {
        if (res.code === 200) {
          this.getSearchData()
          return Promise.resolve(true)
        } else {
          return Promise.resolve(false)
        }
      })
    },
    addConfig (form) {
      this.$API.postConfigurationApproval(form).then(res => {
        if (res.code === 200) {
          this.getSearchData()
          this.isShowDialog(false)
          this.$message({
            message: '操作成功',
            type: 'success'
          })
        }
      })
    },
    editConfig (form) {
      console.log('========', form)
      this.$API.putConfigurationApprovalId(form.id, form).then(res => {
        if (res.code === 200) {
          this.getSearchData()
          this.isShowDialog(false)
          this.$message({
            message: '操作成功',
            type: 'success'
          })
        }
      })
    }
  }
}
</script>



